  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  background-image: linear-gradient(127deg, #384DFF 0%, #24CDFF 91%);
  z-index: 2;
  opacity: 0.85;
}
.page .cover .page-top {
  position: absolute;
  width: 100%;
  bottom: 50px;
