.hover-overly-left-to-right {
	position: relative;
	overflow: hidden;
}

.hover-overly-left-to-right::before {
	position: absolute;
	top: 0;
	left: -100%;
	display: block;
	content: "";
	width: 50%;
	height: 100%;
	background: linear-gradient(to right, rgba(255, 255, 255, 0.7) 0%, rgba(255, 255, 255, 0.3) 100%);
	-webkit-transform: skewX(25deg);
	transform: skewX(25deg);
	z-index: 2;
}

.hover-overly-left-to-right:hover:before {
	-webkit-animation: shine 1.5s;
	animation: shine 1.5s;
}

.hover-overly-zoom {
	position: relative;
	overflow: hidden;
}

.hover-overly-zoom:before {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 1;
	display: block;
	content: "";
	width: 0px;
	height: 0px;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	opacity: 0;
	background: rgba(255, 255, 255, 0.2);
	border-radius: 7px;
}

.hover-overly-zoom:hover:before {
	-webkit-animation-duration: 0.95s;
	animation-duration: 0.95s;
	-webkit-animation-timing-function: ease;
	animation-timing-function: ease;
	-webkit-animation-delay: 0s;
	animation-delay: 0s;
	-webkit-animation-iteration-count: 1;
	animation-iteration-count: 1;
	-webkit-animation-direction: normal;
	animation-direction: normal;
	-webkit-animation-fill-mode: none;
	animation-fill-mode: none;
	-webkit-animation-play-state: running;
	animation-play-state: running;
	-webkit-animation-name: circle;
	animation-name: circle;
}

