.parallax {
  min-height: 280px;
  position: relative;
}
.parallax:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.78;
  background-image: linear-gradient(127deg, #384DFF 0%, #24CDFF 91%);
  z-index: 2;
}
.parallax .parallax-content {
  position: relative;
  z-index: 3;
}
.parallax .parallax-content .count-item {
  height: 280px;
  position: relative;
  overflow: hidden;
}
.parallax .parallax-content .count-item:hover strong {
  margin-top: 60px;
}
.parallax .parallax-content .count-item:before {
  content: "";
  position: absolute;
  width: 1px;
  height: 40px;
  background: #fff;
  left: 0px;
  right: 0px;
  margin: auto;
  top: 0px;
}
.parallax .parallax-content .count-item:after {
  content: "";
  position: absolute;
  width: 1px;
  height: 40px;
  background: #fff;
  left: 0px;
  right: 0px;
  margin: auto;
  bottom: 0px;
}
.parallax .parallax-content .count-item strong {
  display: block;
  text-align: center;
  font-weight: 400;
  font-size: 40px;
  letter-spacing: 1.72px;
  margin-bottom: 10px;
